<template>
  <div>
  </div>
</template>

<script>
export default {
  name: "refresh-page",
  created() {
    let jumpPath=this.$route.params.nextPath;
    let self=this;
    let clearId=setTimeout(function(){
      clearTimeout(clearId);
      self.$router.replace({ name: jumpPath });
    },100);
  }
};
</script>

<style>

</style>


